.App{
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 500px;
  height: fit-content;
  margin: 120px auto;
  padding: 10px;
  border-radius: 8px;
  background: url(scribb.jpg) no-repeat center center fixed; 
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  background-size: cover;
  background-color: #210124;
  color: #000000;
  padding: 20px;
  font-family: 'Mooli', sans-serif;
}
.logo {
  width: 100px;
  height: auto;
  margin-right: 20px;
}
.App h1{
  text-align: center;
  color: #000000;
}
.form{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 12px
}
.form input[type="text"]{
  padding:10px;
  width: 85%;
  border: 1px solid #080000;
  border-radius: 6px;
}
.form input[type="text"]:focus,
.form button[type="submit"]:focus{
  outline:none;
}
.form button[type="submit"]{
  background-color: #800060;
  color: #dadfba;
  border: none;
  border-radius: 6px;
  padding: 10px;
  cursor: pointer
}
.todo-listItems{
  width: 100%;
  display: flex;
  flex-direction: column;
}
.todo-item{
  display:flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px
}
.item-content{
  width: 60%
}
.todo-item button{
  border: none;
  border-radius: 6px;
  cursor: pointer;
  padding: 8px
}
.todo-item button:focus{
  outline:none
}
.update-item{
  background-color: rgb(51, 218, 32);
  color:#fff;
}
.delete-item{
  background-color: red;
  color:#fff;
}
.update-form{
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.update-new-input{
  width: 80%;
  border-radius: 6px;
  border: 1px solid #eee;
  padding: 8px;
  outline: none;
}
.update-new-btn{
  background-color: rgb(34, 7, 123);
  color: #fff;
  cursor: pointer;
}